import { store, view } from '@risingstack/react-easy-state';


class Counter extends React.Component {
  counter = store({
    num: 0,
    increment: () => this.counter.num++,
    decrement: () => this.counter.num--,
  });

  componentDidMount() {
    this.autoUpdate();
  }

  autoUpdate() {
    const date = new Date();
    const hour = date.getHours();
    if (hour >= 9 && hour <= 17) {
      this.counter.increment();
    }
    if (hour <= 8 || hour >= 18) {
      this.counter.decrement();
    }
    setTimeout(this.autoUpdate.bind(this), 3600000);
  }

  render() {
    return (
      <div style={styles.container}>
        <div>
          <h1 style={styles.title}>Simple Counter</h1>
          <h3 style={styles.subtitle}>By Adam Gienapp</h3>
        </div>

        <div style={styles.number}>{this.counter.num}</div>

        <div style={styles.buttonContainer}>
          <button onClick={this.counter.increment}>+</button>
          <button onClick={this.counter.decrement}>-</button>
        </div>
        
        <style jsx global>{`
          body {
            background: steelblue;
            color: #fff;
          }
        `}</style>
      </div>
    );
  }
}

export default view(Counter);


const styles = {
  container: {
    width: "70%",
    minWidth: "500px",
    margin: "0 auto",
  },

  title: {
    width: "500px",
    fontSize: "66px",
    marginBottom: "0",
    textAlign: "center",
  },

  subtitle: {
    width: "470px",
    marginTop: "0",
    textAlign: "right",
  },

  number: {
    fontSize: "150px",
    textAlign: "center",
    margin: "50px 0",
  },

  buttonContainer: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-around",
  }
}